import { Component, OnInit } from '@angular/core';
import { MetadataService, ITool, IFacetCategory, IFacetValue } from '../core/metadata.service';
import { PageService } from '../core/page.service';
import { Breakpoints, BreakpointObserver } from '@angular/cdk/layout';
import { map, shareReplay } from 'rxjs/operators';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  tools: ITool[];
  facets: IFacetCategory[];
  filters: IFacetValue[] = [];
  title = 'All Tools';
  description = 'Search all tools available from Flux Tools.'

  constructor(private breakpointObserver: BreakpointObserver, private metadata: MetadataService, private page: PageService) {
    this.onSearchChange();
  }

  ngOnInit(): void {
    this.page.set({
      title: this.title,
      description: this.description
    });
  }

  numberOfColumns = this.breakpointObserver.observe([
    Breakpoints.Handset,
    Breakpoints.XSmall,
    Breakpoints.Small,
    Breakpoints.Medium,
    Breakpoints.Large,
    Breakpoints.XLarge
  ]).pipe(
    map(({ matches }) => {
      if (this.breakpointObserver.isMatched(Breakpoints.XSmall)) {
        return 2;
      }
      if (this.breakpointObserver.isMatched(Breakpoints.Small)) {
        return 2;
      }
      if (this.breakpointObserver.isMatched(Breakpoints.Medium)) {
        return 4;
      }
      return 4;
    })
  );

  showSidebar$: Observable<boolean> = this.breakpointObserver.observe([Breakpoints.XSmall])
    .pipe(
      map(result => !result.matches),
      shareReplay()
    );


  clear() {
    this.filters = [];
    this.onSearchChange();
  }

  onSearchRemove(value: IFacetValue) {
    const index = this.filters.indexOf(value);

    if (index >= 0) {
      this.filters.splice(index, 1);
    }

    this.onSearchChange();
  }

  onSearchChange() {
    [this.tools, this.facets] = this.metadata.getSearchFacets(this.filters);
  }
}
